.grid-center {
  position: absolute;
  width: 120px;
  height: 140px;
  display: flex;
  gap: 10px;
  flex-direction: column;
  align-items: center;
}

.players {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  background-color: rgba(0,0,0,.7);
  border-radius: 5px;
  padding: 5px;
}

.dices {
  width: 120px;
  cursor: pointer;
}
