.grid {
  background-image: url('../../assets/wood.jpg');
  background-size: cover;
  width: 780px;
  height: 780px;
  display: grid;
  grid-template-rows: repeat(19, 40px);
  grid-template-columns: repeat(19, 40px);
  gap: 1px;
  box-sizing: border-box;
  position: relative;
}

.row {
  box-sizing: border-box;
}

.cell {
  outline: 1px solid #000;
  box-shadow: 0 0 10px 0 #fff inset;
  text-align: center;
  align-content: center;
  box-sizing: border-box;
}

.cell.inactive {
  outline: 0;
  box-shadow: 0 0 0 0 transparent;
}

.user-logo {
  width: 40px;
  height: 40px;
}

.login-logo {
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.user-info {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  font-weight: 500;
  position: relative;
}

.user-name {
  white-space: nowrap;
}

.button {
  padding: 5px 10px;
  white-space: nowrap;
}

.p0 {
  fill: #ffffff;
  color: #ffffff;
  stroke: #ffffff;
}

.p1 {
  fill: #ff0000;
  color: #ff0000;
  stroke: #ff0000;
}

.p2 {
  fill: #0087ff;
  color: #0087ff;
  stroke: #0087ff;
}

.p3 {
  fill: #179934;
  color: #179934;
  stroke: #179934;
}

.p4 {
  fill: #fff000;
  color: #fff000;
  stroke: #fff000;
}